.body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 18px;
}

button {
    padding: 16px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 16px;
    background-color: black;
    color: white;
    border-radius: 8px;
    cursor: pointer;
}

.website-title {
    font-size: 50px;
    font-family: 'Ink Free';
    text-align: center;
}

h3, h4 {
    margin-bottom: 0;
}

nav {
    /*border-bottom: 4px solid black;*/
    text-align: center;
    position: fixed;
    top: 0;
    width: 100%;
    background-color: whitesmoke;
    font-family: 'Yu Gothic';
    
}

    nav ul {
        list-style-type: none;
        margin: 0;
        padding: 0;
    }

    nav li {
        display: inline-block;
        padding-top: 24px;
        padding-bottom: 24px;
    }

    nav a {
        text-decoration: none;
        color: black;
        font-weight: normal;
        padding: 24px 32px;
    }

        nav a:hover {
            background: #DCDCDC;
            color: white;
        }

#page-body {
    /*max-width: 700px;*/
    margin: auto;
    padding-top: 72px;
    padding-left: 72px;
    padding-right: 72px;
}

.article-list-item {
    text-decoration: none;
    color: black;
    padding: 16px 0 0 0;
}

    .article-list-item p {
        padding-bottom: 32px;
        border-bottom: 2px solid black;
    }

#add-comment-form {
    border-bottom: 2px solid black;
    padding: 16px;
}

    #add-comment-form h3 {
        margin-bottom: 16px;
    }

input, textarea {
    display: block;
    width: 300px;
    padding: 8px;
    border-radius: 8px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 18px;
    border: 2px solid black;
    margin-bottom: 16px;
}

.error {
    padding: 8px;
    border-radius: 4px;
    background-color: pink;
}

#add-comment-form button {
    width: 320px;
}

#upvotes-section button, p {
    display: inline-block;
    margin-right: 16px;
}

.comment {
    border-bottom: 2px solid black;
    padding: 16px;
}

.nav-right {
    position: absolute;
    top: 8px;
    right: 8px;
}

.gallery-scroll-parent {
    overflow: auto;
    white-space: nowrap;
    padding-bottom: 25px;
   
}


::-webkit-scrollbar {
    display: none;
}

.gallery {
    padding-left: 6px;
    padding-right: 6px;
    box-shadow: 0 15px 20px -14px rgba(0,0,0,0.25);
}

h1 {
    font-family: 'Ink Free';
}

/* https://codepen.io/mcraiganthony/pen/NxGxqm */

@gray-darker: #444444;
@gray-dark: #696969;
@gray: #999999;
@gray-light: #cccccc;
@gray-lighter: #ececec;
@gray-lightest: lighten(@gray-lighter,4%);

*,
*::before,
*::after {
    box-sizing: border-box;
}



.btn--block {
    display: block;
    width: 100%;
}

.cards {
    /*display: flex;*/
    flex-wrap: wrap;
    list-style: none;
    margin: 0;
    padding: 0;
    overflow-x: auto;
}

.cards__item {
    display: inline-block;
    padding: 1rem;

    @media(min-width: 40rem) {
        width: 50%;
    }

    @media(min-width: 56rem) {
        width: 33.3333%;
    }
    overflow-x: auto;
}

.card {
    background-color: white;
    border-radius: 0.25rem;
    box-shadow: 0 10px 15px -14px rgba(0,0,0,0.25);
    display: flex;
    flex-direction: column;
    overflow: hidden;
 
}
    .card:hover {
        box-shadow: 0 15px 20px -14px rgba(0,0,0,0.25);
    }

.card__content {
    display: flex;
    flex: 1 1 auto;
    flex-direction: column;
    padding: 1rem;
  
}

.card__image {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    /*border-top-left-radius: 0.25rem;
    border-top-right-radius: 0.25rem;*/
    border-radius: 0.25rem;
    filter: contrast(100%);
   /* //filter: saturate(180%);*/
    overflow: auto;
    position: relative;
    transition: filter 0.5s cubic-bezier(.43,.41,.22,.91);
    width: auto;
    &::before {
        content: "";
       /* display: block; */
        padding-top: 56.25%;
    }

    @media(min-width: 40rem) {
        &::before {
            padding-top: 66.6%;
        }
    }
&:hover {
    .card__image

{
    filter: contrast(100%);
}

}
}



.card__title {
    color: @gray-dark;
    font-size: 0.90rem;
    font-weight: 300;
    letter-spacing: 2px;
    text-transform: uppercase;
    text-align: center;
    padding-top: 10px;
    font-family: 'Ink Free';
    &:hover

{
    .card__image

{
    filter: contrast(100%);
}

}
}

.card__text {
    flex: 1 1 auto;
    font-size: 0.875rem;
    line-height: 1.5;
    margin-bottom: 1.25rem;
    word-wrap: break-word;
}

body {
    background-color: whitesmoke;
}

p {
    font-family: 'Yu Gothic';
}